'use client';

import { ColumnDef } from '@tanstack/react-table';
import { BeneficiaryMessage } from '@/lib/generated/prisma';
import { Button } from '@/components/ui/button';

type BeneficiaryMessageWithRelations = BeneficiaryMessage & {
  beneficiary?: {
    id: string;
    firstName: string;
    lastName: string;
  };
};

type Opts = {
  onEdit: (row: BeneficiaryMessageWithRelations) => void;
  onDelete?: (id: string) => void;
  /** pass the logged-in user's role; when "beneficiary", the Delete column is omitted */
  currentUserRole?: string;
};

export function beneficiaryMessageColumns({
  onEdit,
  onDelete,
  currentUserRole,
}: Opts): ColumnDef<BeneficiaryMessageWithRelations>[] {
  const cols: ColumnDef<BeneficiaryMessageWithRelations>[] = [
    {
      accessorKey: 'title',
      header: 'Title',
      cell: ({ row }) => row.getValue('title'),
    },
    {
      accessorKey: 'beneficiaryMessageText',
      header: 'Message',
      cell: ({ row }) => {
        const msg = row.getValue('beneficiaryMessageText');
        if (!msg) return '';
        if (typeof msg === 'string') return msg;
        try {
          return JSON.stringify(msg);
        } catch {
          return '[object]';
        }
      },
    },
    {
      accessorKey: 'beneficiary',
      header: 'Beneficiary',
      cell: ({ row }) => {
        const b = row.original.beneficiary;
        return b ? `${b.firstName} ${b.lastName}` : '-';
      },
    },
    {
      accessorKey: 'status',
      header: 'Status',
      cell: ({ row }) => (row.getValue('status') as string) || '-',
    },
    {
      accessorKey: 'createdAt',
      header: 'Created At',
      cell: ({ row }) =>
        row.getValue('createdAt')
          ? new Date(row.getValue('createdAt') as string).toLocaleDateString()
          : '',
    },
    {
      accessorKey: 'updatedAt',
      header: 'Updated At',
      cell: ({ row }) =>
        row.getValue('updatedAt')
          ? new Date(row.getValue('updatedAt') as string).toLocaleDateString()
          : '',
    },
    {
      id: 'actions',
      header: 'Actions',
      cell: ({ row }) => (
        <Button type="button" size="sm" variant="outline" onClick={() => onEdit(row.original)}>
          Edit
        </Button>
      ),
    },
  ];

  // Hide Delete for beneficiaries
  if (onDelete && currentUserRole !== 'beneficiary') {
    cols.push({
      id: 'delete',
      header: 'Delete',
      cell: ({ row }) => (
        <Button
          type="button"
          size="sm"
          variant="destructive"
          onClick={() => onDelete(row.original.id)}
        >
          Delete
        </Button>
      ),
    });
  }

  return cols;
}
