'use client';

import React from 'react';
import { DataTable } from '@/app/(admin)/admin/dashboard/data-table/data-table/data-table';
import { beneficiaryMessageColumns } from '@/app/(admin)/admin/dashboard/data-table/columns/beneficiaryMessages';

export default function BeneficiaryMessagesSection({
  paginatedData,
  page,
  rowsPerPage,
  handleEdit,
  handleDelete,
  currentUserRole,
  TableActions,
}: {
  paginatedData: any[];
  page: number;
  rowsPerPage: number;
  handleEdit: (record: any) => void;
  handleDelete: (id: string | number) => void;
  currentUserRole: string;
  TableActions: React.FC<any>;
}) {
  const columns = [
    {
      id: 'number',
      header: 'No.',
      cell: ({ row }: any) => (page - 1) * rowsPerPage + row.index + 1,
      size: 50,
    },
    ...beneficiaryMessageColumns({
      onEdit: handleEdit,
      onDelete: handleDelete,
      currentUserRole,
    }),
  ];

  return (
    <>
      <DataTable columns={columns} data={paginatedData} />
      <TableActions data={paginatedData} columns={columns} tableRef={React.createRef()} />
    </>
  );
}
