'use client';

import { ColumnDef } from '@tanstack/react-table';
import { Beneficiary } from '@/lib/generated/prisma';
import { Button } from '@/components/ui/button';

type BeneficiaryWithRelations = Beneficiary & {
  createdBy?: {
    firstName: string;
    lastName: string;
  };
  institution?: {
    id: string;
    name: string;
  };
  beneficiaryMessages?: { id: string; title?: string }[];
  beneficiaryRequests?: { id: string; title?: string }[];
  messagesCount?: number;
  requestsCount?: number;
};

type Opts = {
  onEdit: (beneficiary: BeneficiaryWithRelations) => void;
  onDelete?: (beneficiaryId: string) => void;
  onAddMessage?: (beneficiaryId: string) => void;
  onAddRequest?: (beneficiaryId: string) => void;
  onMessageIdClick?: (msgId: string) => void;
  onRequestIdClick?: (reqId: string) => void;
  messagesCountMap?: Record<string, number>;
  requestsCountMap?: Record<string, number>;
  /** Pass the logged-in user's role so the column factory can hide Delete for beneficiaries */
  currentUserRole?: string;
};

export function beneficiaryColumns({
  onEdit,
  onDelete,
  onAddMessage,
  onAddRequest,
  onMessageIdClick,
  onRequestIdClick,
  messagesCountMap,
  requestsCountMap,
  currentUserRole,
}: Opts): ColumnDef<BeneficiaryWithRelations>[] {
  const cols: ColumnDef<BeneficiaryWithRelations>[] = [
    {
      accessorKey: 'firstName',
      header: 'First Name',
      cell: ({ row }) => (
        <div className="whitespace-normal break-words max-w-xs">{row.getValue('firstName')}</div>
      ),
    },
    {
      accessorKey: 'lastName',
      header: 'Last Name',
      cell: ({ row }) => (
        <div className="whitespace-normal break-words max-w-xs">{row.getValue('lastName')}</div>
      ),
    },
    {
      accessorKey: 'gender',
      header: 'Gender',
      cell: ({ row }) => {
        const val = row.getValue('gender') as string | undefined;
        return val ? val[0].toUpperCase() + val.slice(1) : '';
      },
    },
    {
      accessorKey: 'dateOfBirth',
      header: 'Date of Birth',
      cell: ({ row }) =>
        row.getValue('dateOfBirth')
          ? new Date(row.getValue('dateOfBirth') as string).toLocaleDateString()
          : '',
    },
    {
      accessorKey: 'email',
      header: 'Email',
      cell: ({ row }) => (row.getValue('email') as string) || '-',
    },
    {
      accessorKey: 'phone',
      header: 'Phone',
      cell: ({ row }) => (row.getValue('phone') as string) || '-',
    },
    {
      accessorKey: 'institution',
      header: 'Institution',
      cell: ({ row }) => {
        const institution = row.original.institution;
        return institution ? institution.name : '-';
      },
    },
    {
      id: 'messagesCount',
      header: 'Messages',
      cell: ({ row }) =>
        messagesCountMap && row.original.id ? messagesCountMap[row.original.id] || 0 : 0,
    },
    {
      id: 'requestsCount',
      header: 'Requests',
      cell: ({ row }) =>
        requestsCountMap && row.original.id ? requestsCountMap[row.original.id] || 0 : 0,
    },
    {
      accessorKey: 'createdAt',
      header: 'Created At',
      cell: ({ row }) =>
        row.getValue('createdAt')
          ? new Date(row.getValue('createdAt') as string).toLocaleDateString()
          : '',
    },
    {
      accessorKey: 'updatedAt',
      header: 'Updated At',
      cell: ({ row }) =>
        row.getValue('updatedAt')
          ? new Date(row.getValue('updatedAt') as string).toLocaleDateString()
          : '',
    },
    {
      id: 'createdBy',
      header: 'Created By',
      cell: ({ row }) => {
        const createdBy = row.original.createdBy;
        if (!createdBy) return '-';
        return `${createdBy.firstName} ${createdBy.lastName}`;
      },
    },
    {
      id: 'actions',
      header: 'Actions',
      cell: ({ row }) => (
        <div className="flex flex-wrap gap-2">
          <Button type="button" size="sm" variant="outline" onClick={() => onEdit(row.original)}>
            Edit
          </Button>

          {onAddMessage && (
            <Button
              type="button"
              size="sm"
              className="px-2 py-1 rounded bg-green-600 text-white hover:bg-green-700 text-xs"
              onClick={() => onAddMessage(row.original.id)}
            >
              Add Message
            </Button>
          )}

          {onAddRequest && (
            <Button
              type="button"
              size="sm"
              className="px-2 py-1 rounded bg-green-600 text-white hover:bg-green-700 text-xs"
              onClick={() => onAddRequest(row.original.id)}
            >
              Add Request
            </Button>
          )}
        </div>
      ),
    },
  ];

  // ⛔ Hide Delete column for beneficiaries
  if (onDelete && currentUserRole !== 'beneficiary') {
    cols.push({
      id: 'delete',
      header: 'Delete',
      cell: ({ row }) => (
        <Button
          type="button"
          size="sm"
          variant="destructive"
          onClick={() => onDelete(row.original.id)}
        >
          Delete
        </Button>
      ),
    });
  }

  return cols;
}
