'use client';

import React from 'react';
import { DataTable } from '@/app/(admin)/admin/dashboard/data-table/data-table/data-table';
import { beneficiaryRequestColumns as baseBeneficiaryRequestColumns } from '@/app/(admin)/admin/dashboard/data-table/columns/beneficiaryRequests';

export default function BeneficiaryRequestsSection({
  paginatedData,
  page,
  rowsPerPage,
  handleEdit,
  handleDelete,
  responsesCountMap,
  TableActions,
  onRespond,
}: {
  paginatedData: any[];
  page: number;
  rowsPerPage: number;
  handleEdit: (record: any) => void;
  handleDelete: (id: string | number) => void;
  responsesCountMap: Record<string, number>;
  TableActions: React.FC<any>;
  onRespond: (id: string) => void;
}) {
  const columns = [
    {
      id: 'number',
      header: 'No.',
      cell: ({ row }: any) => (page - 1) * rowsPerPage + row.index + 1,
      size: 50,
    },
    ...baseBeneficiaryRequestColumns({
      onEdit: handleEdit,
      onDelete: handleDelete,
    }),
    {
      id: 'responsesCount',
      header: 'Responses',
      cell: ({ row }: any) => responsesCountMap[row.original.id] || 0,
    },
    {
      id: 'respond',
      header: 'Respond',
      cell: ({ row }: any) => (
        <button
          className="px-2 py-1 rounded bg-green-600 text-white hover:bg-green-700 text-xs"
          onClick={() => onRespond(row.original.id)}
        >
          Respond
        </button>
      ),
    },
  ];

  return (
    <>
      <DataTable columns={columns} data={paginatedData} />
      <TableActions data={paginatedData} columns={columns} tableRef={React.createRef()} />
    </>
  );
}
